<template>
    <div class="reset-pwd-page">
        <!-- <van-nav-bar
            class="nav-bar"
            :title="title"
            :border="false"
            left-arrow
            @click-left="onBack"
        /> -->
        <navigation-bar  @onLeftClick="onBack" :pageName="title" ></navigation-bar>

        <div class="setpwd-content">
            <div class="phone-number">
                <input 
                type="tel"
                class="input-phone"
                placeholder="输入手机号"
                maxlength="11"
                v-model="phoneNum"
                onkeyup="value=value.replace(/[^\d]/g,'')"
                />
            </div>
            <div class="phone-authcode">
                <input 
                type="tel"
                class="input-authcode"
                placeholder="输入验证码"
                maxlength="6"
                v-model="phoneAuthCode"
                onkeyup="value=value.replace(/[^\d]/g,'')"
                />
                <div class="get-verification" :style="{color: verifyStrColor}" @click="getPhoneValit">{{verifyCodeStr}}</div>
            </div>
            <div style="height:0px; overflow: hidden;">
                <input 
                type="tel"
                v-model="phoneAuthCode"
                onkeyup="value=value.replace(/[^\d]/g,'')"
                />
            </div>
            <div class="new-password">
                <input 
                type="password"
                class="input-password"
                placeholder="输入新密码（6-16位字符）"
                maxlength="16"
                v-model="password"
                />
            </div>
            <div 
            class="ok-btn"
            :style="{'background-color': okBtnBackground, 'box-shadow': okBtnShadow}"
            @click="clickedokBtn">
            确认
            </div>
        </div>
        <!-- 弹出框 -->
        <van-dialog 
        v-model="isShowDialog"
        confirmButtonColor="#17B7BD"
        :message="dialogMessage"
        show-cancel-button
        :beforeClose="beforeClose"/>
    </div>
</template>

<script>

import {
    getUserExitsService,
    getPhoneValitService,
    requestModifyOPwdService,
    getRsaPublicKey
} from "../../services/loginService";

import Vue from 'vue';
import {Toast} from 'vant';
Vue.use(Toast);
import NavigationBar from '@/common/currency/NavigationBar.vue';

export default {
    components:{
        NavigationBar
    },
    name: "resetPwd",
    mounted () {
        // 获取传参
        let routerParams = this.$route.params;
        if (routerParams != undefined
        && routerParams.phoneNum != undefined) {
            this.phoneNum = routerParams.phoneNum;
        }
        this.getPubKey();
    },
    data() {
        return {
            title: '重置密码',
            verifyCodeStr: '获取验证码',
            verifyStrColor: '#17B7BD',
            phoneNum: '',
            phoneAuthCode: '',
            password: '',
            //确认按钮是否可点击
            okBtnEnable: false,
            //是否显示提示框
            isShowDialog: false,
            //提示框内容
            dialogMessage: '',
            publicKey: ""
        };
    },
    watch: {
        // 监听手机号输入
        phoneNum: function(phoneNum) {
            this.changngeLoginBtnStatus();
        },
        //监听验证码输入
        phoneAuthCode: function(authCode) {
            this.changngeLoginBtnStatus();
        },
        //监听密码输入
        password: function(pwd) {
            this.changngeLoginBtnStatus();
        },
    },
    computed:{
        //确认按钮背景色
        okBtnBackground: function() {
            return this.okBtnEnable ? '#17B7BD' : '#D0CDCE';
        },
        okBtnShadow: function() {
            return this.okBtnEnable ? '0 0 15px 0 rgba(197,58,72,0.39)' : ' 0 0 15px 0 #D0CDCE';
        }
    },
    methods: {
        //修改登录按钮状态
        changngeLoginBtnStatus() {
            this.okBtnEnable = this.phoneNum.length >= 11 && this.phoneAuthCode.length >= 6  && this.password.length >= 6;
        },
        clickedokBtn: function() {
            console.log('点击确认按钮');
            if (!this.okBtnEnable) {
                return;
            }
            // let reqData = {
            //     PHONE: this.phoneNum,
            //     OWNER_PWD: this.password,
            //     VERIFICATION: this.phoneAuthCode
            // }
            let basePassWord =  btoa(this.password);
            let rsaInfo = this.phoneNum+","+this.phoneAuthCode+","+basePassWord;
            let rsaPassWord = Vue.prototype.$getRSASign(rsaInfo,this.publicKey);
            let reqData = {
                modifyInfo : rsaPassWord
            }
            requestModifyOPwdService(reqData)
            .then(noteData => {
                this.showMsg(noteData.msg);
                if (noteData.resultCode == 0) {
                    this.onBack();
                }
            })
            .catch(error => {
                this.showMsg(error);
            });
        },
        // 获取验证码
        getPhoneValit: function() {
            if (this.verifyCodeStr != '获取验证码') {
                return;
            }
            if (this.phoneNum === undefined || this.phoneNum.length == 0) {
                this.showMsg("请输入手机号");
                return;
            }
            let phoneNumreg = /^1[0-9]{10}$/;
            if (!phoneNumreg.test(this.phoneNum)) {
                this.showMsg("请输入有效的手机号码");
                return;
            }
            let reqData = {
                PHONE: this.phoneNum
            };
            //检测用户是否存在
            getUserExitsService(reqData)
            .then(noteData => {
                if (noteData.success == "1") {
                    //成功
                    this.getPhoneAuthCode();
                }
                else {
                    //失败
                    this.dialogMessage = '该手机号尚未注册，请通过验证码登录';
                    this.isShowDialog = true;
                }
            })
            .catch(error => {
                this.showMsg(error);
            });
        },
        //获取验证码
        getPhoneAuthCode: function() {
            let reqData = {
                PHONE: this.phoneNum
            };
            getPhoneValitService(reqData)
            .then(noteData => {
            if (noteData.success == false) {
                //失败
                this.showMsg(noteData.msg);
            }
            else {
                //成功
                this.showMsg('验证码已发送');
                //修改颜色
                this.verifyStrColor = '#bdbdbd';
                let totalTime = 60;
                this.verifyCodeStr = totalTime + "秒";
                let clock = window.setInterval(() => {
                totalTime--;
                if (totalTime > 0) {
                    this.verifyCodeStr = totalTime + "秒";
                }
                else {
                    window.clearInterval(clock);
                    //修改颜色
                    this.verifyStrColor = '#17B7BD';
                    this.verifyCodeStr = "获取验证码";
                }
                }, 1000);
            }
            })
            .catch(error => {
                this.showMsg(error);
            });
        },
        // 退出登录弹框回调
        beforeClose: function(action, done) {
            if (action === "confirm") {
                this.$store.dispatch('changeActiveTabFun', 0);
                //跳转验证码登录
                this.onBack();
            } 
            done();
        },
        onBack() {
            this.$router.back(-1);
        },
        showMsg: function(msg){
            this.$toast(msg);
        },
        getPubKey(){
            getRsaPublicKey().then(res => {
                this.publicKey = res.data.publicKey;
            }).catch(error => {
                this.isLoading =false;
            });
        },
    }
  };
</script>

<style scoped lang="scss">
    $navHeight: 44px;
    $inputDivHeight: 54px;
    .reset-pwd-page {
        box-sizing: border-box;
        position: absolute;
        overflow: hidden;;
        width: 100%;
        height: 100%;
        background-color: #fff;
        display: flex;
        flex-direction: column;
    }
    /* 导航 */
    .navBar {
        position: relative;
        width: 100%;
        height: $navHeight;
        z-index: 66;
    }
    input {
        border:0;
        outline:none;
        background-color: transparent;
        caret-color:#17B7BD;
    }
    input::-webkit-input-placeholder {
        /* placeholder颜色  */
        color: #9A9A9A;
        /* placeholder字体大小  */
        font-size: 14px;
        font-family: PingFangSC-Medium;
        /* placeholder位置  */
        text-align: left;
    }
    .setpwd-content {
        box-sizing: border-box;
        flex: 1;
        .phone-number,
        .phone-authcode,
        .new-password {
            box-sizing: border-box;
            background: #F8F8F8;
            border-radius: 27px;
            margin: 10px 40px;
            height: $inputDivHeight;
            display: flex;
            align-items: center;
        }
        .phone-number {
            margin-top: 58px;
        }
        .input-phone,
        .input-authcode,
        .input-password {
            padding: 0px 32px;
            font-family: PingFangSC-Regular;
            font-size: 14px;
            height: 32px;
            line-height: 32px;
            flex: 1;
            color: #333333;
        }
        .get-verification {
            position: absolute;
            width: 100px !important;
            height: $inputDivHeight;
            line-height: $inputDivHeight;
            font-family: PingFangSC-Medium;
            font-size: 14px;
            color: #17B7BD;
            text-align: left;
            right: 40px;
        }
    }
    .setpwd-content .ok-btn {
        font-family: PingFangSC-Medium;
        font-size: 16px;
        width:46%;
        height: 44px;
        line-height: 44px;
        text-align: center;
        color: #f2f2f2;
        margin: 5% 27% 0;
        overflow: hidden;
        border-radius: 22px;
    }
</style>
